export default {
    data() {
        return {
            sidenav: false
        }
    },
    methods: {
        toggleslde() {
            this.sidenav === false ? this.sidenav = true : this.sidenav = false;
        },
        moveToRoute(route) {
            this.$router.push(route)
        }
    }
}
