import adminMixin from '@/mixins/admin.js';

    export default {
        data() {
            return {
                email: '',
                password: '',
                loading: false
            }
        },
        mixins: [adminMixin],
        methods: {
            moveToRoute(route) {
                this.$router.push(route)
            },
            signin() {
                const {
                    email, 
                    password
                } = this;

                const credentials = {
                    email,
                    password,
                }

                this.loading = true;

                this.authenticate(credentials, 'api/signinadmin');
            },
        },
        watch: {
            user: function(newVal, oldVal) {
                if (newVal) {
                    if (newVal._id) {
                        this.$router.push('/admin/dashboard');
                    } else {
                        this.$router.push('/admin');
                    }
                }
            }
        }
    }
